"use client";

import { signOut } from "next-auth/react";
import { Button } from "@heroui/react";
import { LogOut } from "lucide-react";

export function LogoutButton() {
    return (
        <Button
            isIconOnly
            variant="light"
            color="danger"
            onPress={() => signOut({ callbackUrl: "/login" })}
        >
            <LogOut className="w-5 h-5" />
        </Button>
    );
}
