"use client";

import { useState, useMemo } from "react";
import {
    Table, TableHeader, TableColumn, TableBody, TableRow, TableCell,
    Button, Input, Card, CardBody, Divider
} from "@heroui/react";
import { Trash2, Plus, Minus, ShoppingCart } from "lucide-react";
import { ScannerListener } from "./ScannerListener";
import { getProductByBarcode, completeSale } from "@/lib/actions";

interface CartItem {
    id: string;
    productId: string;
    barcode: string;
    name: string;
    price: number;
    quantity: number;
}

export function POSSystem({ cashierId }: { cashierId: string }) {
    const [cart, setCart] = useState<CartItem[]>([]);
    const [manualBarcode, setManualBarcode] = useState("");
    const [isCheckingOut, setIsCheckingOut] = useState(false);

    const total = useMemo(() => {
        return cart.reduce((sum, item) => sum + item.price * item.quantity, 0);
    }, [cart]);

    const addToCart = async (barcode: string) => {
        const product = await getProductByBarcode(barcode);

        if (!product) {
            alert("Product not found!");
            return;
        }

        setCart(prev => {
            const existing = prev.find(item => item.barcode === barcode);
            if (existing) {
                return prev.map(item =>
                    item.barcode === barcode
                        ? { ...item, quantity: item.quantity + 1 }
                        : item
                );
            }
            return [...prev, {
                id: Math.random().toString(),
                productId: product.id,
                barcode: product.barcode,
                name: product.name,
                price: product.price,
                quantity: 1
            }];
        });
    };

    const handleCheckout = async () => {
        if (cart.length === 0) return;

        setIsCheckingOut(true);
        const result = await completeSale(cashierId, cart.map(item => ({
            productId: item.productId,
            quantity: item.quantity,
            price: item.price
        })), total);

        if (result.success) {
            alert("Sale completed successfully!");
            setCart([]);
        } else {
            alert("Checkout failed: " + result.error);
        }
        setIsCheckingOut(false);
    };

    const removeItem = (id: string) => {
        setCart(prev => prev.filter(item => item.id !== id));
    };

    const updateQuantity = (id: string, delta: number) => {
        setCart(prev => prev.map(item => {
            if (item.id === id) {
                const newQty = Math.max(1, item.quantity + delta);
                return { ...item, quantity: newQty };
            }
            return item;
        }));
    };

    return (
        <div className="flex flex-col lg:flex-row gap-6 p-6 h-[calc(100vh-100px)]">
            <ScannerListener onScan={addToCart} />

            {/* Items List */}
            <div className="flex-grow overflow-auto">
                <Card className="h-full">
                    <CardBody>
                        <div className="flex justify-between items-center mb-4">
                            <h2 className="text-xl font-bold flex items-center gap-2">
                                <ShoppingCart className="w-5 h-5" /> Current Cart
                            </h2>
                            <div className="flex gap-2">
                                <Input
                                    size="sm"
                                    placeholder="Manual barcode..."
                                    value={manualBarcode}
                                    onChange={(e) => setManualBarcode(e.target.value)}
                                    onKeyDown={(e) => {
                                        if (e.key === 'Enter') {
                                            addToCart(manualBarcode);
                                            setManualBarcode("");
                                        }
                                    }}
                                />
                                <Button size="sm" color="primary" onPress={() => {
                                    addToCart(manualBarcode);
                                    setManualBarcode("");
                                }}>Add</Button>
                            </div>
                        </div>

                        <Table aria-label="POS Cart Table" removeWrapper>
                            <TableHeader>
                                <TableColumn>PRODUCT</TableColumn>
                                <TableColumn>PRICE</TableColumn>
                                <TableColumn>QTY</TableColumn>
                                <TableColumn>SUBTOTAL</TableColumn>
                                <TableColumn>ACTIONS</TableColumn>
                            </TableHeader>
                            <TableBody emptyContent={"No items in cart. Start scanning!"}>
                                {cart.map((item) => (
                                    <TableRow key={item.id}>
                                        <TableCell>
                                            <div>
                                                <p className="font-semibold">{item.name}</p>
                                                <p className="text-xs text-zinc-500">{item.barcode}</p>
                                            </div>
                                        </TableCell>
                                        <TableCell>₱{item.price.toFixed(2)}</TableCell>
                                        <TableCell>
                                            <div className="flex items-center gap-2">
                                                <Button isIconOnly size="sm" variant="flat" onPress={() => updateQuantity(item.id, -1)}>
                                                    <Minus className="w-4 h-4" />
                                                </Button>
                                                <span className="w-8 text-center font-bold">{item.quantity}</span>
                                                <Button isIconOnly size="sm" variant="flat" onPress={() => updateQuantity(item.id, 1)}>
                                                    <Plus className="w-4 h-4" />
                                                </Button>
                                            </div>
                                        </TableCell>
                                        <TableCell className="font-bold text-primary">
                                            ₱{(item.price * item.quantity).toFixed(2)}
                                        </TableCell>
                                        <TableCell>
                                            <Button isIconOnly color="danger" variant="light" onPress={() => removeItem(item.id)}>
                                                <Trash2 className="w-4 h-4" />
                                            </Button>
                                        </TableCell>
                                    </TableRow>
                                ))}
                            </TableBody>
                        </Table>
                    </CardBody>
                </Card>
            </div>

            {/* Checkout Section */}
            <div className="w-full lg:w-96 flex flex-col gap-4">
                <Card className="bg-primary/5 border-primary/20">
                    <CardBody className="gap-4">
                        <h3 className="text-lg font-bold">Checkout Summary</h3>
                        <Divider />
                        <div className="flex justify-between items-center text-sm">
                            <span>Subtotal</span>
                            <span>₱{total.toFixed(2)}</span>
                        </div>
                        <div className="flex justify-between items-center text-sm">
                            <span>Tax (0%)</span>
                            <span>₱0.00</span>
                        </div>
                        <Divider />
                        <div className="flex justify-between items-center text-2xl font-black text-primary">
                            <span>TOTAL</span>
                            <span>₱{total.toFixed(2)}</span>
                        </div>
                        <Button
                            color="primary"
                            size="lg"
                            className="w-full font-bold mt-4"
                            isDisabled={cart.length === 0}
                            isLoading={isCheckingOut}
                            onPress={handleCheckout}
                        >
                            COMPLETE SALE
                        </Button>
                    </CardBody>
                </Card>
            </div>
        </div>
    );
}
